@import "helpers/all";
@import "generated/sprite-svg";
@import "common";
@import "layouts";
@import "main";

//////////////////////////////////////////////////////////////////////
//////////////////////////////////////////////////////////////////////
////////////////	MAIN    //////////////////////////////////////////
//////////////////////////////////////////////////////////////////////
//////////////////////////////////////////////////////////////////////
* {
	font-size: 14px;
	font-family: sans-serif; }
.wrap {
	display: flex;
	flex-direction: column;
	height: 100vh; }

.container {
	max-width: 1000px;
	margin: 0 auto;
	@include r(1020) {
		margin: 0 20px; } }

.call, .btn {
	padding: 15px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	color: #fff;
	background: #54cfee;
	text-decoration: none;
	border-radius: 5px 5px 5px 0;

	&__img {
		padding-right: 14px;
		margin-right: 15px;
		position: relative;
		.icon {
			fill: #fff;
			font-size: 24px; } } }

//////////////////////////////////////////////////////////////////////
//////////////////////////////////////////////////////////////////////
////////////////	HEADER  //////////////////////////////////////////
//////////////////////////////////////////////////////////////////////
//////////////////////////////////////////////////////////////////////

.header {
	margin: 15px 20px;
	background: linear-gradient(rgba(0, 0, 0, .1) 5%, transparent 15%, transparent 90%, rgba(0, 0, 0, .1) 100%);
	border-radius: 20px;
	@include r(650) {
		margin: 0;
		padding-bottom: 30px;
		border-radius: 20px 20px 0 0; }

	.container {
		max-width: 100vw;
		display: flex;
		align-items: center;
		justify-content: space-between;
		position: relative;
		@include r(1050) {
			flex-direction: column;
			justify-content: space-around;
			margin-right: 0;
			margin-left: 0; } } }

.logo {
	margin-left: 5px;
	height: 50px;
	@include r(1050) {
		left: 0%;
		position: absolute;
		bottom: 0; }
	@include r(650) {
		position: relative;
		margin-top: 10px;
		transform: translate(0, 0); } }

.menu {
	flex: 2;
	margin: 0 40px;
	max-width: 600px;
	&__text {
		color: #fff;
		position: relative;
		display: inline-block;
		padding: 5px;
		@include r(650) {
			width: 200px;
			font-size: 1.3rem;
			text-align: center; } }
	@include r(900) {
		max-width: 100%;
		margin-bottom: 20px; }
	@include r(400) {
		margin: 0;
		margin-bottom: 30px; }

	ul {
		display: flex;
		justify-content: space-around;
		@include r(650) {
			display: flex;
			flex-direction: column;
			align-items: center; }

		li {
			display: inline-block;
			padding: 5px;
			a {
				font-size: 12px;
				font-weight: bold;
				position: relative;
				color: #000F;
				padding: 5px;
				margin: 5px;
				line-height: 2;
				text-decoration: none;
				border-radius: 3px;
				// box-shadow: 0 0 40px rgba(0,204,0,.4)
				background: rgba(77, 93, 132, .9);
				&:hover,&:active {
					animation: bouncing .35s cubic-bezier(.651,.125,.61,.1) .21s infinite alternate both;
					transition: all .4s ease-in-out;
					margin-top: 100px; }
				@include r(650) {
					max-width: 500px; } } } } }

.call {
	max-width: 250px;
	transition: all .3s;
	&__img {
		.icon {
			animation: bouncing 0.5s cubic-bezier(.651,.125,.61,.1) .6s infinite alternate both;
			border-radius: 50%;
			padding: .2em;
			display: inline-block;
			position: relative; } }
	&__text {
		text-align: right; }
	&__number {
		text-align: right;
		font-size: 24px;
		line-height: 1; }
	&__phone {
		position: relative;
		&:after {
			height: 100%;
			width: 1px;
			background: rgba(77, 93, 132, .9);
			content: '';
			position: absolute;
			left: -15px;
			top: 0; } }
	&:hover {
		transform: scale(1.05, 1.05); }
	@include r(1050) {
		align-self: center;
		// left: 50%
		// transform: translate(-50%, 0)
 } }		// position: relative




@keyframes bouncing {
	0% {
  		bottom: 0; box-shadow: 0 0 5px rgba(0,0,0,0.5); }
	100% {
		bottom: 5px; box-shadow: 0 5px 5px rgba(0,0,0,0.1); } }

//////////////////////////////////////////////////////////////////////
//////////////////////////////////////////////////////////////////////
////////////////	HERO    //////////////////////////////////////////
//////////////////////////////////////////////////////////////////////
//////////////////////////////////////////////////////////////////////
.hero {
	height: 100%;
	padding-top: 55px;
	padding-bottom: 55px;
	position: relative;

	&:before {
		content: '';
		height: 20px;
		width: 100%;
		position: relative;
		background: teal; }
	@include r(650) {
		&:before {
			content: '';
			position: absolute;
			bottom: 0;
			top: 0;
			height: 100%;
			width: 100%;
			background: rgba(0,0,0, .8); } }
	.container {
		max-width: 700px;
		margin: 0 auto; }
	&__title {
		text-align: center;
		font-size: 45px;
		color: #282827;
		line-height: 1.4;
		margin-bottom: 40px;
		font-weight: bold;
		padding: 10px;
		position: relative;
		@include r(700) {
			font-size: 3em;
			margin-bottom: 20px; }
		@include r(650) {
			color: #FFF; } }
	&__btns {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		position: relative;
		@include r(650) {
			flex-direction: column;
			align-items: start; } }

	&__or {
		color: rgba(0,0,0, .75);
		text-transform: uppercase;
		font-weight: bold;
		font-size: 2em;
		@include r(650) {
			text-align: center;
			width: 100%;
			margin: 15px auto;
			color: #FFF;
			opacity: .7; } } }

////////////////////////////////////////////////////////////////////////////////////////
//  BUTTON HERO
////////////////////////////////////////////////////////////////////////////////////////

.basicBox {
	line-height: 1;
	position: relative;
	background: #fff;
	margin: 125px auto;
	@include r(650) {
		margin: 0 auto; } }
.btn {
	@include r(650) {
		align-self: start; }
	&__text {
		font-size: 22px;
		font-weight: bold;
		margin-left: 10px; }
	&__img {
		position: relative;
		.icon {
			font-size: 30px; }
		&:after {
			height: 100%;
			width: 1px;
			background: #fff;
			content: '';
			position: absolute;
			right: 0;
			top: 0; } } }
.btn-left {
	border-radius: 0;
	background: rgba(0, 204, 00, .8);
	&:hover {
		box-shadow: 0 0 3px #000; } }
.btn-right {
	background: #fff;
	color: #666666; }
.btn-right.btn {
	.icon {
		fill: #666666; }
	.btn__img:after {
		background: #666666; } }
.ramka {
	rect {
		stroke-width: 5;
		stroke-dasharray: 35, 245;
		stroke-dashoffset: 38;
		transition: all .3s ease-in-out; } }
.ramka, .ramka-left {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	fill: none;
	width: 100%;
	height: 100%;
	stroke: transparent;
	stroke-width: 3;
	rect {
		stroke-dasharray: 400, 0;
		transition: all .2s ease-in-out; }
	&:hover {
		transition: all .15s ease-in-out;
		box-shadow: 0 0 20px #000; } }



.ramka-left {
	&:hover {
		stroke: #fff; } }
.ramka-right {
	&:hover {
		stroke: #4274D3; } }
