html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-phone {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-play {
  width: 1em;
  height: 1em;
  fill: #4e4e50;
}

.icon-rocket {
  width: 1em;
  height: 1em;
  fill: initial;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.block {
  display: flex;
}

* {
  font-size: 14px;
  font-family: sans-serif;
}

.wrap {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
}

@media only screen and (max-width: 1020px) {
  .container {
    margin: 0 20px;
  }
}

.call, .btn {
  padding: 15px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  background: #54cfee;
  text-decoration: none;
  border-radius: 5px 5px 5px 0;
}

.call__img, .btn__img {
  padding-right: 14px;
  margin-right: 15px;
  position: relative;
}

.call__img .icon, .btn__img .icon {
  fill: #fff;
  font-size: 24px;
}

.header {
  margin: 15px 20px;
  background: linear-gradient(rgba(0, 0, 0, 0.1) 5%, transparent 15%, transparent 90%, rgba(0, 0, 0, 0.1) 100%);
  border-radius: 20px;
}

@media only screen and (max-width: 650px) {
  .header {
    margin: 0;
    padding-bottom: 30px;
    border-radius: 20px 20px 0 0;
  }
}

.header .container {
  max-width: 100vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

@media only screen and (max-width: 1050px) {
  .header .container {
    flex-direction: column;
    justify-content: space-around;
    margin-right: 0;
    margin-left: 0;
  }
}

.logo {
  margin-left: 5px;
  height: 50px;
}

@media only screen and (max-width: 1050px) {
  .logo {
    left: 0%;
    position: absolute;
    bottom: 0;
  }
}

@media only screen and (max-width: 650px) {
  .logo {
    position: relative;
    margin-top: 10px;
    transform: translate(0, 0);
  }
}

.menu {
  flex: 2;
  margin: 0 40px;
  max-width: 600px;
}

.menu__text {
  color: #fff;
  position: relative;
  display: inline-block;
  padding: 5px;
}

@media only screen and (max-width: 650px) {
  .menu__text {
    width: 200px;
    font-size: 1.3rem;
    text-align: center;
  }
}

@media only screen and (max-width: 900px) {
  .menu {
    max-width: 100%;
    margin-bottom: 20px;
  }
}

@media only screen and (max-width: 400px) {
  .menu {
    margin: 0;
    margin-bottom: 30px;
  }
}

.menu ul {
  display: flex;
  justify-content: space-around;
}

@media only screen and (max-width: 650px) {
  .menu ul {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.menu ul li {
  display: inline-block;
  padding: 5px;
}

.menu ul li a {
  font-size: 12px;
  font-weight: bold;
  position: relative;
  color: #000F;
  padding: 5px;
  margin: 5px;
  line-height: 2;
  text-decoration: none;
  border-radius: 3px;
  background: rgba(77, 93, 132, 0.9);
}

.menu ul li a:hover, .menu ul li a:active {
  animation: bouncing 0.35s cubic-bezier(0.651, 0.125, 0.61, 0.1) 0.21s infinite alternate both;
  transition: all .4s ease-in-out;
  margin-top: 100px;
}

@media only screen and (max-width: 650px) {
  .menu ul li a {
    max-width: 500px;
  }
}

.call {
  max-width: 250px;
  transition: all .3s;
}

.call__img .icon {
  animation: bouncing 0.5s cubic-bezier(0.651, 0.125, 0.61, 0.1) 0.6s infinite alternate both;
  border-radius: 50%;
  padding: .2em;
  display: inline-block;
  position: relative;
}

.call__text {
  text-align: right;
}

.call__number {
  text-align: right;
  font-size: 24px;
  line-height: 1;
}

.call__phone {
  position: relative;
}

.call__phone:after {
  height: 100%;
  width: 1px;
  background: rgba(77, 93, 132, 0.9);
  content: '';
  position: absolute;
  left: -15px;
  top: 0;
}

.call:hover {
  transform: scale(1.05, 1.05);
}

@media only screen and (max-width: 1050px) {
  .call {
    align-self: center;
  }
}

@keyframes bouncing {
  0% {
    bottom: 0;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  }
  100% {
    bottom: 5px;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1);
  }
}

.hero {
  height: 100%;
  padding-top: 55px;
  padding-bottom: 55px;
  position: relative;
}

.hero:before {
  content: '';
  height: 20px;
  width: 100%;
  position: relative;
  background: teal;
}

@media only screen and (max-width: 650px) {
  .hero:before {
    content: '';
    position: absolute;
    bottom: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.8);
  }
}

.hero .container {
  max-width: 700px;
  margin: 0 auto;
}

.hero__title {
  text-align: center;
  font-size: 45px;
  color: #282827;
  line-height: 1.4;
  margin-bottom: 40px;
  font-weight: bold;
  padding: 10px;
  position: relative;
}

@media only screen and (max-width: 700px) {
  .hero__title {
    font-size: 3em;
    margin-bottom: 20px;
  }
}

@media only screen and (max-width: 650px) {
  .hero__title {
    color: #FFF;
  }
}

.hero__btns {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  position: relative;
}

@media only screen and (max-width: 650px) {
  .hero__btns {
    flex-direction: column;
    align-items: start;
  }
}

.hero__or {
  color: rgba(0, 0, 0, 0.75);
  text-transform: uppercase;
  font-weight: bold;
  font-size: 2em;
}

@media only screen and (max-width: 650px) {
  .hero__or {
    text-align: center;
    width: 100%;
    margin: 15px auto;
    color: #FFF;
    opacity: .7;
  }
}

.basicBox {
  line-height: 1;
  position: relative;
  background: #fff;
  margin: 125px auto;
}

@media only screen and (max-width: 650px) {
  .basicBox {
    margin: 0 auto;
  }
}

@media only screen and (max-width: 650px) {
  .btn {
    align-self: start;
  }
}

.btn__text {
  font-size: 22px;
  font-weight: bold;
  margin-left: 10px;
}

.btn__img {
  position: relative;
}

.btn__img .icon {
  font-size: 30px;
}

.btn__img:after {
  height: 100%;
  width: 1px;
  background: #fff;
  content: '';
  position: absolute;
  right: 0;
  top: 0;
}

.btn-left {
  border-radius: 0;
  background: rgba(0, 204, 0, 0.8);
}

.btn-left:hover {
  box-shadow: 0 0 3px #000;
}

.btn-right {
  background: #fff;
  color: #666666;
}

.btn-right.btn .icon {
  fill: #666666;
}

.btn-right.btn .btn__img:after {
  background: #666666;
}

.ramka rect {
  stroke-width: 5;
  stroke-dasharray: 35, 245;
  stroke-dashoffset: 38;
  transition: all .3s ease-in-out;
}

.ramka, .ramka-left {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  fill: none;
  width: 100%;
  height: 100%;
  stroke: transparent;
  stroke-width: 3;
}

.ramka rect, .ramka-left rect {
  stroke-dasharray: 400, 0;
  transition: all .2s ease-in-out;
}

.ramka:hover, .ramka-left:hover {
  transition: all .15s ease-in-out;
  box-shadow: 0 0 20px #000;
}

.ramka-left:hover {
  stroke: #fff;
}

.ramka-right:hover {
  stroke: #4274D3;
}
